@import "../../../../../main.module.scss";

.main {
  width: calc(100% - 20px);
  min-height: 100%;
  height: auto;
  background-color: $blue;
  padding: 80px 0 0 40px;
  border-radius: 24px;
  display: flex;
  margin-bottom: 10px;
  margin-left: 10px;

  @media (max-width: 700px) {
    flex-direction: column;
    padding: 80px 40px 0 40px;

  }

  * {
    color: white;
  }

  .wrapperDescriptionCourses {
    width: 50%;
    padding-bottom: 40px;

    @media (max-width: 700px) {
      width: 100%;

    }

    h3 {
      margin-bottom: 10px;
      font-size: 18px;

    }

    h1 {
      font-size: 32px;
      margin-bottom: 10px;

    }

    p {
      margin-bottom: 10px;
    }

    button {
      border-radius: 8px;
      padding: 10px 20px;
      font-weight: 600;
      border: none;
      background-color: $red;
    }

  }

    img {
      width: 50%;
      height: auto;
      object-fit: cover;

      @media (max-width: 700px) {
        width: 100%;

      }
    }

  .error {
    color: black;
  }


}